      $.post($.SP + '/mobile/task/getMobileTaskVO.do', { id: this.id }, function (response) {
        var root = response.root;
        //root.handle_contact='2222';
        for (var x in root) {
          vue.$data[x] = root[x];
        }

        $.hidePreloader();
        //initevent();
      });
